.content-area.login_register-page{
  padding: 120px 0;
  .box-container{
    max-width: 700px;
    margin: 0 auto;
    border-radius: 6px;
  }
  h2{
    font-size: 2rem; 
    text-align: center;
    margin-bottom: 2rem;
  }

  .login-grid{
    display: grid;
    @media screen and (max-width: 425px) {
      display: flex;
      flex-direction: column;
    }
    &.login{
      grid-template-columns: 1fr 2px 1fr;
      grid-gap: 20px;
    }
    &.register{
      grid-template-columns: 1fr;
        grid-gap: 20px;
        padding: 25px 43px;
    }
    
    .line{
      width: 2px;
      height: 100%;
      background: #e5e5e5;
    }
    form{
      display: flex;
      flex-direction: column;
      button {
          border-radius: 50px;
          background: transparent;
          padding: 10px 50px;
          border: 2px solid #DC6180;
          color: #DC6180;
          font-size: 12px;
          text-transform: uppercase;
          transition-property: background,color;
          transition-duration: .2s;
          &:hover {
              background: #DC6180;
              color: white;
              border: 2px solid #DC6180;
          }
      }
      a{
        margin-bottom: 1rem;
      }
      
    }
    .social-accounts{
      display: flex;
      flex-direction: column;
      a{
        background: #e5e5e5;
        width: 100%;
        font-size: 1rem;
        padding: 10px;
        margin: 0 0 1rem;
        text-align: center;
        color: rgb(31, 31, 31);
        text-decoration: none;
        display: flex;
        justify-content: center;
        align-items: center;
        &:hover {
          opacity: .9 !important
        }
        i {
          margin: 0 15px 0 0;
        }
      }
    }
    
  }
  .footer{
      margin: 1rem auto;
      text-align: center;
      color: #6c6c6c;
      a{
        text-decoration: none;
      }
  }
}



